import React from 'react'
import EascLogo from '../../assets/images/easc_logo.png'
import { ReactComponent as ModuleLinkIcon } from '../../assets/images/moduleLink.svg'
import { RouteComponentProps } from 'react-router-dom'
import NavBar from './NavBar'
import { ReactComponent as SwapIcon } from '../../assets/images/swap.svg'
import { ReactComponent as NftIcon } from '../../assets/images/nft.svg'
import { ReactComponent as PoolIcon } from '../../assets/images/pool.svg'
import {
  HomeContainer,
  Header,
  HeaderInner,
  Main,
  MainInnter,
  StyledDesc,
  TextWrapper,
  StyledEascModules,
  StyledModule,
  ModuleTopWrapper,
  ModuleLabel,
  ModuleIcon
} from './styles'
import { SearchInput } from '../../components/SearchInput'

export const EASC_MODULE_INFO = [
  {
    key: 'swap',
    label: 'Swap',
    icon: <SwapIcon />
  },
  {
    key: 'nft',
    label: 'NFT',
    icon: <NftIcon />
  },
  {
    key: 'pool',
    label: 'Pool',
    icon: <PoolIcon />
  }
]

const Home = ({ history }: RouteComponentProps) => {
  return (
    <HomeContainer>
      <Header>
        <HeaderInner>
          <img src={EascLogo} alt="logo" width={223} height={56} />
          <NavBar />
          <SearchInput type="text" placeholder="请输入" />
        </HeaderInner>
      </Header>
      <Main>
        <MainInnter>
          <StyledDesc>
            <TextWrapper color="#A1ADD5">Welcome to the</TextWrapper>
            <div>
              <TextWrapper color="#4965FC">EASCORswap</TextWrapper>
              <TextWrapper leftSpace={10}>Network</TextWrapper>
            </div>
          </StyledDesc>
          <StyledEascModules>
            {EASC_MODULE_INFO.map(({ key, label, icon }) => {
              return (
                <StyledModule
                  key={key}
                  onClick={() => {
                    if (key === 'swap') {
                      history.push('/pool')
                    }
                  }}
                >
                  <ModuleTopWrapper>
                    <ModuleLabel>{label}</ModuleLabel>
                    <ModuleLinkIcon />
                  </ModuleTopWrapper>
                  <ModuleIcon>{icon}</ModuleIcon>
                </StyledModule>
              )
            })}
          </StyledEascModules>
        </MainInnter>
      </Main>
    </HomeContainer>
  )
}
export default Home
